.main{
  overflow: hidden;
  padding: 7em 0 4em;
  &__title{
    font-size: 3.25em;
    line-height: 120%;
    .inline-swiper{
      display: inline-block;
      color: $secondary;
    }
  }
  &__desc{
    font-size: 1.125em;
    line-height: 150%;
    color: #555;
    margin-top: 2em;
    font-weight: 300;
    max-width: 31.22em;
  }
  &__bottom{
    margin-top: 4.875em;
    position: relative;
  }
  &__nav{
    display: flex;
    margin-top: 3.125em;
  }
  &__btn{
    height: 4.625em;
    padding: 0 3.125em;
    span{
      font-size: 1.25em;
    }
  }
  &__link{
    margin-top: -1.19em;
  }
}

.inline-swiper{
  max-width: 7.5em;
  overflow: hidden;
  transform: translateY(.25em);
  .swiper{
    &-container{
      width: 7.5em;
    }
    &-slide{
      width: 8em;
    }
  }
}

.main-portfolio{
  .portfolio-items{
    margin-top: 1.625em;
  }
}

.portfolio-items{
  row-gap: 5.625em;
}

.portfolio-card{
  display: block;
  &__img{
    position: relative;
    padding-bottom: 75.8%;
    img{
      position: absolute;
      top: 5em;
      left: 2.375em;
      width: calc(100% - 4.75em);
    }
  }
  &__content{
    margin-top: 1.875em;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__info{
    @include grid100gap(.25em);
  }
  &__desc{
    color: #9f9f9f;
    font-weight: 300;
  }
  &__btn{
    display: flex;
    align-items: center;
    color: #444546;
    margin-left: .75em;
    flex-shrink: 0;
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
    svg{
      width: 3.25em;
      margin-left: .9375em;
    }
    &:hover{
      svg{
        transform: translateX(.25em);
      }
    }
  }
}

.text-bg{
  width: 100%;
  z-index: -1;
  pointer-events: none;
  img{
    width: 100%;
    display: block;
  }
  &--portfolio{
    position: absolute;
    top: 0;
    left: 0;
    margin-top: -9em;
  }
  &--service{
    position: absolute;
    top: 0;
    left: 0;
  }
  &--contacts{
    margin-top: -.1875em;
  }
}

.services{
  padding: 4em 0 5.1875em;
  &__inner{
    position: relative;
    padding-top: 8em;
  }
}

.feedback{
  padding: 5.1875em 0 2.25em;
  &__desc{
    max-width: 30em;
    line-height: 130%;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 2em;
  }
  &__link{
    font-size: 1.75em;
    line-height: 122%;
    @include lastM(0 0 .21em);
    display: block;
  }
}


@media screen and (max-width: 980px){
  .main{
    padding: 4.35em 0 1.875em;
    &__title{
      font-size: 1.875em;
    }
    &__desc{
      font-size: 1em;
      line-height: 150%;
      margin-top: 1.5em;
    }
    &__bottom{
      margin-top: 5.625em;
    }
    &__nav{
      margin-top: 2.5em;
    }
    &__btn{
      height: 3.8125em;
      padding: 0 2.75em;
      span{
        font-size: 1em;
      }
    }
  }

  .main-portfolio{
    .portfolio-items{
      margin-top: 1.0625em;
    }
  }

  .portfolio-items{
    row-gap: 3.125em;
  }

  .portfolio-card{
    &__img{
      img{
        top: 2.625em;
        left: 1.25em;
        width: calc(100% - 2.5em);
      }
    }
    &__content{
      margin-top: 1em;
    }
  }

  .text-bg{
    &--portfolio{
      margin-top: -4.75em;
    }
    &--contacts{
      margin-top: .25em;
    }
  }

  .services{
    padding: 1.875em 0 2.25em;
    &__inner{
      padding-top: 5em;
      row-gap: .875em;
    }
  }

  .feedback{
    padding: 1.875em 0 2.125em;
    &__desc{
      line-height: 137%;
    }
    &__links{
      margin-top: 1em;
    }
    &__link{
      font-size: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__top{
      gap: 1.875em;
      position: relative;
      padding-bottom: 6.25em;
      &-col1{
        order: 2;
      }
    }
    &__nav{
      position: absolute;
      left: 0;
      bottom: 0;
      margin: 0;
    }
  }

  .text-bg{
    &--portfolio{
      margin-top: -2.625em;
    }
  }

  .services{
    &__inner{
      padding-top: 2.25em;
    }
  }
}
