.main-counts{
  row-gap: 3.44em;
}

.count-item{
  &__num{
    font-size: 4.375em;
    line-height: 120%;
  }
  &__title{
    line-height: 140%;
    font-size: 1.25em;
  }
}

.odometer{
  overflow: hidden;
  padding-right: 2px;
  text-align: center;
}

.odometer.odometer-auto-theme, .odometer.odometer-theme-default{
  //font-family: $font1;
}

.odometer-digit{

}

.odometer-value.odometer-last-value{
  width: .62em;
  text-align: center;
}

.odometer-digit-inner{
  overflow: unset !important;
  text-align: center !important;
  @include sameNumWidth;
}


@media screen and (max-width: 980px){
  .main-counts{
    grid-template-columns: repeat(2,calc(50% - .25em));
    gap: 1.5em .5em;
  }

  .count-item{
    &__num{
      font-size: 3.125em;
    }
    &__title{
      font-size: .875em;
    }
  }
}


