.services-prices{
  padding: 4.1875em 0;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link{
    display: flex;
    align-items: center;
    svg{
      width: 4.3125em;
      margin-left: .9375em;
    }
    &:hover{
      path{
        stroke: $primary;
      }
      svg{
        transform: translateX(.25em);
      }
    }
  }
  &__items{
    margin-top: 1.75em;
  }
}

.service-card{
  @include grid100gap(.3125em);
  border-top: 1px solid #666666;
  padding: 1.625em 0;
  &:last-child{
    border-bottom: 1px solid #666666;
  }
  &__desc{
    font-size: 1em;
    line-height: 125%;
    font-weight: 300;
    color: #999999;
  }
}

.clients{
  padding: 4.1875em 0;
  overflow: hidden;
  &__content{
    display: flex;
    margin-top: 3.125em;
  }
  &__items{
    display: flex;
    animation: 30s slides infinite linear;
  }
  .client-item{
    margin-right: 6.25em;
    flex-shrink: 0;
  }
}

@keyframes slides {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}


.client-item{
  &__img{
    height: 2.875em;
    img{
      height: 100%;
      display: block;
    }
  }
}

.user-feedback{
  padding: 4.1875em 0 2.75em;
  &__inner{
    padding: 4.625em 3.75em 5.5em;
    background-color: #F7F7F7;
    margin-top: 1.75em;
    display: grid;
    grid-template-columns: 29.5em 36em;
    justify-content: space-between;
    gap: 1.75em;
  }
  &__col{
    display: flex;
    flex-direction: column;
  }
  @media screen and (min-width: 577px){
    .contacts-form{
      grid-template-columns: repeat(2, 1fr);
      gap: .875em 2.25em;
    }
    .fg{
      input{
        font-size: 1.125em;
      }
    }
  }
  .m-check{
    max-width: 31em;
  }
  &__text{
    @include grid100gap(1.5em);
  }
  &__desc{
    line-height: 150%;
    color: #424344;
  }
  &__bottom{
    padding-top: 2em;
    margin-top: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__subtitle{
    color: #777777;
  }
  &__link{
    margin-top: .33em;
  }
}

._full{
  grid-column: 1/-1;
}


@media screen and (max-width: 980px){
  .services-prices{
    padding: 2.5em 0;
    &__top{
      border-top: 1px solid #666666;
      padding-top: 1.5em;
    }
    &__link{
      width: .625em;
      height: 1.125em;
      background-image: url(../img/arrow-right.svg);
      @include bgImgSize(100%);
      span,
      svg{
        display: none;
      }
    }
    &__items{
      margin-top: 1.35em;
    }
  }

  .service-card{
    gap: .44em;
    padding: 1.75em 0;
  }

  .clients{
    padding: 2.5em 0;
    &__content{
      margin-top: 2.25em;
    }
    .client-item{
      margin-right: 2.5em;
    }
  }

  .client-item{
    &__img{
      height: 1.9375em;
    }
  }

  .user-feedback {
    padding: 2.5em 0 11em;
    &__inner {
      grid-template-columns: 100%;
      padding: 3.125em 1.25em 3.5em;
      position: relative;
    }
    &__text{
      gap: 1.25em;
      max-width: 25em;
    }
    &__bottom{
      position: absolute;
      top: 100%;
      left: 0;
      margin-top: 4.875em;
      padding-top: 0;
    }
  }
}


