.how-work{
  padding: 6.125em 0 4.1875em;
}

.stages{
  padding: 5em 3.75em 5.625em;
  background-color: #F7F7F7;
  margin-top: 1.6875em;
  position: relative;
  &__items{
    display: grid;
    grid-template-columns: 25em 25em 25em;
    gap: 6.4em 2em;
    justify-content: space-between;
  }
  @media screen and (min-width: 577px){
    &__title{
      position: absolute;
      top: 5em;
      left: 3.75em;
    }
    &__items{
      &:before{
        content: '';
        width: 100%;
      }
    }
  }
}

.stage-item{
  &__title{
    margin-top: 1.56em;
  }
  &__list{
    margin-top: 1.19em;
    @include grid100gap(.5em);
    color: #555;
    li{
      font-size: 1.125em;
      line-height: 122%;
      padding-left: .94em;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        background-color: $secondary;
        border-radius: 50%;
        width: .39em;
        height: .39em;
      }
    }
  }
}

.advantages{
  padding: 4.1875em 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 8.25em /  4));
    gap: 2.75em;
    margin-top: 2.5em;
  }
}

.advantage-item{
  &__icon{
    height: 5.9375em;
    img{
      height: 100%;
      display: block;
    }
  }
  &__text{
    margin-top: 1.375em;
    @include grid100gap(1.125em);
  }
  &__desc{
    font-size: 1em;
    line-height: 145%;
    color: #555555;
  }
}


@media screen and (max-width: 980px){
  .how-work{
    padding: 3.125em 0 2.5em;
  }

  .stages{
    padding: 3.28em 1.25em 3.75em;
    margin-top: 1.3125em;
    &__items{
      grid-template-columns: repeat(2, calc(50% - 1em));
      gap: 2.59em 2em;
    }
    @media screen and (min-width: 577px){
      &__title{
        position: absolute;
        top: 3.28em;
        left: 1.25em;
      }
    }
  }

  .stage-item{
    &__title{
      margin-top: .81em;
    }
    &__list{
      margin-top: 1.16em;
      gap: .69em;
      li{
        font-size: 1em;
        line-height: 119%;
        padding-left: 1.06em;
        &:before{
          width: .4375em;
          height: .4375em;
        }
      }
    }
  }

  .advantages{
    padding: 2.5em 0 2.625em;
    &__title{
      max-width: 10em;
      line-height: 137%;
    }
    &__grid{
      grid-template-columns: repeat(2, calc(50% - 1.375em));
      margin-top: 1.375em;
    }
  }

  .advantage-item{
    max-width: 20.5em;
  }
}


@media screen and (max-width: 576px){
  .stages {
    &__items {
      grid-template-columns: 100%;
      margin-top: 2.09em;
    }
  }

  .advantages{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
